document.addEventListener('DOMContentLoaded', function() {

    const cardData = [
        { imageUrl: 'assets/dogs.png', title: 'Мальтезе' },
        { imageUrl: '../assets/dogs.png', title: 'Мальтипу'},
        { imageUrl: '../assets/allDogs.svg',  title: 'Пудель'}
    ];


    function createCard(title, imageUrl) {
        const cardContainer = document.getElementById('cardContainer');
        const cardTemplate = document.createElement('div');
        cardTemplate.className = 'card';

        const cardImage = document.createElement('img');
        cardImage.src = imageUrl;
        cardImage.alt = title;

        const cardTitle = document.createElement('h3');
        cardTitle.textContent = title;

        cardTemplate.appendChild(cardImage);
        cardTemplate.appendChild(cardTitle);
        cardContainer.appendChild(cardTemplate);
    }


    for (const card of cardData) {
        createCard(card.title, card.imageUrl);
    }

});
